*, *:before, *:after {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box; }

h1, h2, h3, h4, h5, h6,
p, blockquote, pre,
dl, dd, ol, ul,
form, fieldset, legend,
table, th, td, caption,
hr {
  margin: 0;
  padding: 0; }

input::-moz-focus-inner {
  border: 0;
  padding: 0;
  margin-top: -2px;
  margin-bottom: -2px; }

input {
  border: none; }

ul {
  list-style: none; }

a, a:active, a:visited, a:focus {
  text-decoration: none;
  outline: 0; }

body {
  line-height: 1; }

img {
  display: block;
  max-width: 100%;
  height: auto; }

table th {
  font-weight: normal; }

:focus {
  outline: none; }
